import { login } from "@/api/restaurant/index"
import { Toast } from "antd-mobile";
import { setAuthUser } from "@/reducers/auth";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { Button } from "antd";
import { useState } from "react";
import { askForPermissionToReceiveNotifications } from '@/notification.ts';

export default function Login() {

    const dispatch = useDispatch();
    const history = useHistory();
    const [loading, setLoading] = useState(false);

    function loginHandle(e)
    {
        askForPermissionToReceiveNotifications();
        
        setLoading(true);
        e.preventDefault();

        let formData = new FormData(e.target);
        login(formData)
        .then(res => {
            let data = res?.data?.user;
            localStorage.setItem("user-token", res.data.token);

            if(!data?.restaurant?.length)
                data.restaurant = [res?.data?.user?.restaurant];
            
            dispatch(setAuthUser(data));

            Toast.show({
                icon: 'success',
                content: res?.message
            })

            return history.push("/dashboard");

        }).catch(err => {
            setLoading(false);
            Object.entries( err?.response?.data?.errors ).map(((res: any) => {
                Toast.show({
                    icon: 'fail',
                    content: res[1][0]
                })
            }))
        })
    }


    return (
        <div className="d-flex height-100">
           <div className="login-container m-auto">
                <form className="login form" onSubmit={loginHandle}>
                    <span className="login__icon">Sakesbierre</span>
                    <h2 className="login__title">Reservation Management</h2>
                    <div className="login__row">
                    <label className="login__label" htmlFor="lg-em">
                        e-mail
                    </label>
                    <input
                        className="login__input"
                        id="lg-em"
                        type="email"
                        name="email"
                        placeholder="example@email.com"
                    />
                    </div>
                    <div className="login__row">
                    <label className="login__label" htmlFor="lg-ps">
                        password
                    </label>
                    <input
                        className="login__input"
                        id="lg-ps"
                        name="password"
                        type="password"
                        placeholder="**********"
                    />
                    </div>
                    <div className="login__row">

                    <Button loading={loading} className="login__button" htmlType="submit">
                        Login
                    </Button>

                    </div>
                    <div className="login__row">
                    </div>
                </form>
            </div>

        </div>
    )
}