import { Button, Tabs, Modal, Skeleton } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import React, { useEffect, useRef, useState } from "react";
import { authUser, setAuthUser } from "@/reducers/auth";
import { useDispatch, useSelector } from "react-redux";
import { getReservation, checkout } from "@/api/restaurant";
import { Dialog, Empty, Search, Toast } from "antd-mobile";
import moment from "moment";
import OccupiedTable from "@/assets/occupied-table.svg";
import Sidebar from "@/parts/restaurant/sidebar";

import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import Walkin from "@/components/Restaurant/walkinModal";
import TableModal from "../../../components/Restaurant/tableModal";
import { useHistory, useLocation } from "react-router";
import { LogoutOutlined } from "@ant-design/icons";
import { DialogShowProps } from "antd-mobile/es/components/dialog";

export default function Dashboard() {
  const location = useLocation();
  const AuthSelector = useSelector(authUser);
  const [visibleTable, setTableVisible] = useState(false);
  const tabs = new URLSearchParams(location.search).get("tab") || 1;
  const date = new URLSearchParams(location.search).get("date");
  const history = useHistory();
  const dispatch = useDispatch();

  const [loading, setLoading] = useState(true);
  const [reservations, setReservations] = useState(null);
  const [events, setEvents] = useState();
  const [data, setData] = useState(null);
  const [visible, setVisible] = useState(false);
  const [visibleBooking, setVisibleBooking] = useState(false);

  // START Function for Ant Tabs
  const { TabPane } = Tabs;

  useEffect(() => {
    getReservationFn();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [AuthSelector]);

  function getReservationFn(page = 1, day = "history") {
    setLoading(true);

    let id: any = "";
    id = AuthSelector?.restaurant?.length ? AuthSelector?.restaurant[0].id : "";

    getReservation(id, page, day).then((res) => {
      setReservations(res);
      setLoading(false);
    });
  }

  const handler = useRef<DialogShowProps>();

  const reservationList = (res: any) => (
    <div className="pb-4">
      <div className="">
        <div className="card">
          <div className="card-body">
            <div className="d-flex justify-content-between flex-wrap">
              <div
                className="cursor"
                onClick={() => {
                  setData(res);
                  setVisibleBooking(true);
                }}
              >
                <h5 className="faded pb-1">{res?.name}</h5>
                <p className="faded f-13 ">BID: 00{res?.id}SB</p>
              </div>
              <span className="d-flex gap-20 flex-wrap mt-2 mt-md-0">
                <div
                  className="d-flex call-btn checkout w-auto"
                  onClick={() => {
                    handler.current = Dialog.show({
                      content: (
                        <div className="row">
                          {" "}
                          {res?.table?.map((res, key) => (
                            <div className="position-relative col-6" key={key}>
                              <img src={OccupiedTable} alt="..." width="100%" />
                              <div className="position-absolute seat-text text-center lh-1">
                                <p className="light text-center mb-1 text-uppercase">
                                  {res?.title}
                                </p>
                                <p className="light font-bold mb-0">
                                  {res?.no_of_occupany} Seats
                                </p>
                                <small className="light">
                                  (
                                  {res?.type_id === 1
                                    ? "Non Smoking"
                                    : "Smoking"}
                                  )
                                </small>
                              </div>
                            </div>
                          ))}{" "}
                        </div>
                      ),
                      actions: [
                        {
                          key: "close",
                          text: "Close",
                          onClick: () => {
                            handler.current?.close();
                          },
                        },
                      ],
                    });
                  }}
                >
                  <span className="d-flex flex-column m-auto">
                    <div className="mx-auto">
                      <LogoutOutlined className="f-16 mb-1 text-white" />
                    </div>
                    <p className="text-white">Assigned Table</p>
                  </span>
                </div>

                <div
                  className="d-flex delete-btn checkout"
                  onClick={() => checkoutFn(res?.id)}
                >
                  <span className="d-flex flex-column m-auto">
                    <div className="mx-auto">
                      <LogoutOutlined className="f-16 mb-1 text-danger" />
                    </div>
                    <p className="red">Check Out</p>
                  </span>
                </div>
                <a
                  className="d-flex call-btn"
                  href={`sms:+91${res?.phone}?body=Hello there,`}
                >
                  <span className="d-flex flex-column m-auto">
                    <svg
                      className="mx-auto mb-1"
                      width="18"
                      height="18"
                      viewBox="0 0 18 18"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M17 11.6667C17 12.1382 16.8127 12.5903 16.4793 12.9237C16.1459 13.2571 15.6937 13.4444 15.2222 13.4444H4.55556L1 17V2.77778C1 2.30628 1.1873 1.8541 1.5207 1.5207C1.8541 1.1873 2.30628 1 2.77778 1H15.2222C15.6937 1 16.1459 1.1873 16.4793 1.5207C16.8127 1.8541 17 2.30628 17 2.77778V11.6667Z"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                    </svg>

                    <p className="yellow">SMS</p>
                  </span>
                </a>
                <a href={`tel:+91${res?.phone}`} className="d-flex call-btn">
                  <span className="d-flex flex-column m-auto">
                    <svg
                      className="mx-auto mb-1"
                      width="18"
                      height="18"
                      viewBox="0 0 18 18"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M16.5001 12.69V14.94C16.5009 15.1488 16.4581 15.3556 16.3745 15.547C16.2908 15.7383 16.168 15.9101 16.0141 16.0514C15.8602 16.1926 15.6785 16.3001 15.4806 16.367C15.2828 16.4339 15.0731 16.4588 14.8651 16.44C12.5572 16.1892 10.3403 15.4006 8.39257 14.1375C6.58044 12.9859 5.04407 11.4496 3.89257 9.63745C2.62506 7.68085 1.83625 5.4532 1.59007 3.13495C1.57133 2.92755 1.59598 2.71852 1.66245 2.52117C1.72892 2.32382 1.83575 2.14247 1.97615 1.98867C2.11654 1.83487 2.28743 1.71198 2.47792 1.62784C2.6684 1.5437 2.87433 1.50015 3.08257 1.49995H5.33257C5.69655 1.49637 6.04942 1.62526 6.32539 1.8626C6.60137 2.09994 6.78163 2.42954 6.83257 2.78995C6.92754 3.51 7.10366 4.217 7.35757 4.89745C7.45848 5.16589 7.48032 5.45764 7.4205 5.73811C7.36069 6.01859 7.22172 6.27603 7.02007 6.47995L6.06757 7.43245C7.13524 9.31011 8.68991 10.8648 10.5676 11.9325L11.5201 10.98C11.724 10.7783 11.9814 10.6393 12.2619 10.5795C12.5424 10.5197 12.8341 10.5415 13.1026 10.6425C13.783 10.8964 14.49 11.0725 15.2101 11.1675C15.5744 11.2188 15.9071 11.4024 16.145 11.6831C16.3828 11.9638 16.5092 12.3221 16.5001 12.69Z"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                    </svg>

                    <p className="yellow">Call</p>
                  </span>
                </a>
              </span>
            </div>
            <hr className="main-hr"></hr>
            <section className="d-flex gap-20 flex-wrap">
              <div className="d-flex flex-wrap">
                <div className="d-flex small-icon">
                  <svg
                    width="15"
                    height="15"
                    viewBox="0 0 15 15"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <g clip-path="url(#clip0_128_70)">
                      <path
                        d="M10.5555 12.9999V11.7777C10.5555 11.1294 10.298 10.5076 9.83959 10.0492C9.38116 9.59079 8.75941 9.33325 8.1111 9.33325H3.22222C2.57391 9.33325 1.95215 9.59079 1.49373 10.0492C1.03531 10.5076 0.777771 11.1294 0.777771 11.7777V12.9999"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                      <path
                        d="M5.66661 6.88889C7.01664 6.88889 8.11106 5.79447 8.11106 4.44444C8.11106 3.09441 7.01664 2 5.66661 2C4.31658 2 3.22217 3.09441 3.22217 4.44444C3.22217 5.79447 4.31658 6.88889 5.66661 6.88889Z"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                      <path
                        d="M14.2222 12.9999V11.7777C14.2218 11.2361 14.0415 10.71 13.7097 10.2819C13.3778 9.85385 12.9133 9.54812 12.3888 9.41272"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                      <path
                        d="M9.94443 2.07947C10.4702 2.2141 10.9363 2.5199 11.2691 2.94866C11.6019 3.37742 11.7826 3.90475 11.7826 4.44752C11.7826 4.99029 11.6019 5.51763 11.2691 5.94639C10.9363 6.37515 10.4702 6.68095 9.94443 6.81558"
                        stroke="#EEA959"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      />
                    </g>
                    <defs>
                      <clipPath id="clip0_128_70">
                        <rect
                          width="14.6667"
                          height="14.6667"
                          fill="white"
                          transform="translate(0.166641 0.166626)"
                        />
                      </clipPath>
                    </defs>
                  </svg>
                </div>
                <div className="d-flex ml-2 ">
                  <p className="faded f-14 m-auto">No of People:</p>
                  <p className="faded f-17 pl-1 pt-1 m-auto font-semibold">
                    {res?.no_of_occupancy}
                  </p>
                </div>
              </div>
              <div className="d-flex">
                <div className="d-flex small-icon">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M12.1482 3.25928H3.85187C3.19731 3.25928 2.66669 3.7899 2.66669 4.44446V12.7408C2.66669 13.3953 3.19731 13.9259 3.85187 13.9259H12.1482C12.8027 13.9259 13.3334 13.3953 13.3334 12.7408V4.44446C13.3334 3.7899 12.8027 3.25928 12.1482 3.25928Z"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M10.3704 2.0741V4.44447"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M5.62964 2.0741V4.44447"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M2.66669 6.81494H13.3334"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                </div>

                <div className="d-flex ml-2 ">
                  <p className="faded f-14 m-auto">Date:</p>
                  <p className="faded f-17 pl-1 pt-1 m-auto font-semibold">
                    {moment(res?.date).format("DD-MMM-YYYY")}
                  </p>
                </div>
              </div>
              <div className="d-flex">
                <div className="d-flex small-icon">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.99996 13.9259C11.2728 13.9259 13.9259 11.2728 13.9259 8.00002C13.9259 4.72722 11.2728 2.0741 7.99996 2.0741C4.72716 2.0741 2.07404 4.72722 2.07404 8.00002C2.07404 11.2728 4.72716 13.9259 7.99996 13.9259Z"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M8 4.44446V8.00001L10.3704 9.1852"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                </div>
                <div className="d-flex ml-2 ">
                  <p className="faded f-14 m-auto">Time:</p>
                  <p className="faded f-17 pl-1 pt-1 m-auto font-semibold">
                    {res?.time}
                  </p>
                </div>

                <div className="d-flex small-icon ml-3">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.99996 13.9259C11.2728 13.9259 13.9259 11.2728 13.9259 8.00002C13.9259 4.72722 11.2728 2.0741 7.99996 2.0741C4.72716 2.0741 2.07404 4.72722 2.07404 8.00002C2.07404 11.2728 4.72716 13.9259 7.99996 13.9259Z"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M8 4.44446V8.00001L10.3704 9.1852"
                      stroke="#EEA959"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                </div>
                <div className="d-flex ml-2 ">
                  <p className="faded f-14 m-auto">Seated at:</p>
                  <p className="faded f-17 pl-1 pt-1 m-auto font-semibold">
                    {moment(res?.seated_date).format("hs:ss A")}
                  </p>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    </div>
  );

  useEffect(() => {
    if (tabs === "1") {
      getReservationFn();
    }

    if (tabs === "2") {
      getReservationFn(1, "tomorrow");
    }

    if (tabs === "3") {
      setVisible(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tabs]);

  function callback(key) {
    history.push({
      search: "tab=" + key,
    });
  }

  function handleDateClick(e) {
    let id: any = "";
    id = AuthSelector?.restaurant?.length ? AuthSelector?.restaurant[0].id : "";

    if (id) {
      getReservation(
        id,
        1,
        moment(e.dateStr).format("DD-MM-YYYY"),
        "is_history"
      ).then((res) => {
        setReservations(res);
        setVisible(false);
        setLoading(false);
        
        history.push({
          search: `tab=${tabs}&date=${moment(e.dateStr).format("DD-MM-YYYY")}`,
        });
      });
    }
  }

  function handleMonthChange(e) {
    setLoading(true);

    let id: any = "";
    id = AuthSelector?.restaurant?.length ? AuthSelector?.restaurant[0].id : "";

    getReservation(
      id,
      1,
      moment(e.start).format("DD-MM-YYYY"),
      "is_history_cal"
    ).then((res) => {
      res.map((res: any) => {
        res.title = res.name + " " + res?.time;
        res.date = new Date(res?.date);
      });
      setEvents(res);
    });
    return;
  }

  function checkoutFn(e) {
    checkout(e).then((res) => {
      dispatch(
        setAuthUser({ ...AuthSelector, restaurant: [res?.data?.restaurant] })
      );

      Toast.show({
        icon: "Success",
        content:
          res.message +
          "! Table [" +
          res?.data.rmTable.map((res) => res.title).join(",") +
          "] is Empty.",
      });

      history.push({
        search: "tab=1",
      });
      getReservationFn();
    });
  }

  function search(e) {
    setLoading(true);

    let id: any = "";
    id = AuthSelector?.restaurant?.length
      ? AuthSelector?.restaurant[0]?.id
      : "";

    let day = "history";
    let status = "";

    if (tabs === "3") {
      status = "is_history";
      day = date;
    }

    setTimeout(() => {
      getReservation(id, 1, day, status, e).then((res) => {
        setReservations(res);
        setLoading(false);
      });
    }, 300);
  }

  return (
    <>
      <section className="container-fluid body-dark py-4 px-4">
        <section className="row">
          {/* sidebar  */}
          <Sidebar />

          {/* content */}
          <div className="col-md-10 px-3">
            <div className="row d-flex justify-content-between">
              <div className="col-md-6">
                <h2 className="light">Your History</h2>
                <p className="faded">Hi, Check your History</p>
              </div>
            </div>
            <section className="row mt-4">
              <div className="col-md-12">
                <Tabs defaultActiveKey={tabs.toString()} onChange={callback}>
                  <TabPane tab="All" key="1">
                    <div className="mb-4 col-4 p-0">
                      <Search
                        placeholder="Search For Reservation"
                        onSearch={search}
                        onChange={search}
                      />
                    </div>

                    {loading ? (
                      <Skeleton active />
                    ) : reservations && reservations?.data?.length ? (
                      reservations?.data?.map((res, key) => (
                        <React.Fragment key={key}>
                          {reservationList(res)}
                        </React.Fragment>
                      ))
                    ) : (
                      <div className="card py-4">
                        {" "}
                        <Empty description={"No Reservation Available"} />
                      </div>
                    )}
                  </TabPane>
                  <TabPane
                    tab={<div onClick={() => setVisible(true)}>Calendar</div>}
                    key="3"
                  >
                    <div className="mb-4 d-flex p-0">
                      <Search
                        placeholder="Search For Reservation"
                        onSearch={search}
                        onChange={search}
                      />
                      <Button
                        className="ml-auto my-auto"
                        onClick={() => {
                          setVisible(true);
                        }}
                      >
                        Calendar Filter
                      </Button>
                    </div>

                    {loading ? (
                      <Skeleton active />
                    ) : reservations?.length && reservations ? (
                      reservations?.map((res, key) => (
                        <React.Fragment key={key}>
                          {reservationList(res)}
                        </React.Fragment>
                      ))
                    ) : (
                      <div className="card py-4">
                        {" "}
                        <Empty description={"No Reservation Available"} />
                      </div>
                    )}
                  </TabPane>
                </Tabs>
              </div>
              {/* <div className="col-md-4">
              <Search placeholder="input search text" onSearch={onSearch} />
            </div> */}
            </section>
          </div>
        </section>
      </section>

      {/* calendar modal  */}
      <Modal
        footer={null}
        width={1200}
        destroyOnClose={true}
        visible={visible}
        className="calendar"
        onCancel={() => setVisible(false)}
      >
        <div className="calendar-app">
          <FullCalendar
            headerToolbar={
              {
                // 'left': 'prev,next today',
                // 'center': 'title',
                // 'right': "dayGridMonth,timeGridWeek,timeGridDay"
              }
            }
            events={events}
            dayMaxEvents={true}
            selectMirror={true}
            selectable
            select={handleDateClick}
            eventClick={(info) => {
              info.jsEvent.preventDefault();
              let data =
                info.el.parentElement.parentElement.parentElement.parentElement.getAttribute(
                  "data-date"
                ) ||
                info.el.parentElement.parentElement.parentElement.getAttribute(
                  "data-date"
                );

              let id: any = "";
              id = AuthSelector?.restaurant?.length
                ? AuthSelector?.restaurant[0].id
                : "";

              if (data) {
                getReservation(
                  id,
                  1,
                  moment(data).format("DD-MM-YYYY"),
                  "is_history"
                ).then((res) => {
                  setReservations(res);
                  setVisible(false);
                  setLoading(false);
                  history.push({
                    search: `tab=${tabs}&date=${moment(data).format(
                      "DD-MM-YYYY"
                    )}`,
                  });
                });
              }
            }}
            datesSet={(e) => handleMonthChange(e)}
            dateClick={handleDateClick}
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            initialView="dayGridMonth"
          />
        </div>
      </Modal>

      {/* Booking  */}
      <Walkin
        data={data}
        id={AuthSelector?.restaurant[0]?.id}
        setLoadingContent={setLoading}
        getReservation={getReservation}
        setVisibleBooking={setVisibleBooking}
        setReservations={setReservations}
        visibleBooking={visibleBooking}
      />

      {/* table  */}
      <TableModal
        data={data}
        id={AuthSelector?.restaurant[0]?.id}
        setLoadingContent={setLoading}
        getReservation={getReservation}
        setReservations={setReservations}
        visible={visibleTable}
        setVisible={setTableVisible}
      />
    </>
  );
}
