import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  user: null,
};

const authUserSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    setAuthUser(state, { payload }) {
      state.user = payload;
    },
  },
});

export const { setAuthUser } = authUserSlice.actions;
export const authUser = (auth: any) => auth.authUserReducer.user;
export const restaurant = (auth: any) => auth.authUserReducer?.user?.restaurant[0];


export default authUserSlice.reducer;
