import { Button, Input, Modal, Form, Select, notification } from "antd";
import TableBg from "@/assets/table-bg.svg";
import { Toast, Selector, Stepper, Dialog } from "antd-mobile";
import DatePickerComp from "@/components/Reservation/DatePicker";
import TimePicker from "@/components/Reservation/TimePicker";
import TableType from "@/components/Reservation/TableType";
import { useEffect, useRef, useState } from "react";
import { filledReservation, checkoutOne, searchUserFn, getUsers } from "@/api/reservation/index";
import { DialogShowRef } from "antd-mobile/es/components/dialog";
import { authUser } from "@/reducers/auth";
import { useDispatch, useSelector } from "react-redux";
import { setAuthUser } from "../../reducers/auth";
import moment from "moment";

const Occupancy = ({ value = {}, onChange, initialValue }) => {
  const handler = useRef<DialogShowRef>();
  const [seats, setSeats] = useState(0);
  const [options, setOptions] = useState([]);

  const [number, setNumber] = useState("");

  const triggerChange = (changedValue) => {
    onChange?.({
      number,
      ...value,
      ...changedValue,
    });
    setSeats(number);
  };

  let option = [
    { label: "1", value: "1" },
    { label: "2", value: "2" },
    { label: "3", value: "3" },
    { label: "4", value: "4" },
    { label: "5", value: "5" },
    { label: "6", value: "6" },
    { label: seats > 6 ? seats?.toString() : "+", value: "plus" },
  ];

  useEffect(() => {
    setOptions(option);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (initialValue) {
      stepperChange(initialValue);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialValue]);

  function stepperChange(f: any) {
    setNumber(f.toString());

    triggerChange({
      number: f.toString(),
    });

    let optionNew = [
      { label: "1", value: "1" },
      { label: "2", value: "2" },
      { label: "3", value: "3" },
      { label: "4", value: "4" },
      { label: "5", value: "5" },
      { label: "6", value: "6" },
      { label: f.toString(), value: f.toString() },
      { label: "+", value: "plus" },
    ];

    if (f.toString() > 6) {
      setOptions([...optionNew]);
    }
  }

  function clickDialog(number: any) {
    handler.current?.close();
  }

  const checkManual = (e: any) => {
    if (e[0] === "plus") {
      handler.current = Dialog.show({
        content: (
          <div>
            <label className="mb-3">Number Of Seats</label>

            <Stepper
              defaultValue={7}
              onChange={(f) => stepperChange(f)}
              min={0}
              style={{
                "--border-inner": "none",
                "--button-background-color": "#F8F9FA",
                "--height": "50px",
                "--input-width": "100",
                "--active-border": "1px solid #1677ff",
              }}
            />
          </div>
        ),
        actions: [
          [
            {
              key: "save",
              text: "Ok",
              onClick: () => clickDialog(number),
            },
            {
              key: "close",
              text: "Cancel",
              danger: true,
              onClick: () => {
                handler.current?.close();
              },
            },
          ],
        ],
      });

      triggerChange({
        number: "7",
      });
    } else {
      triggerChange({
        number: e[0],
      });
    }
  };

  return (
    <Selector
      onChange={(e) => checkManual(e)}
      columns={6}
      value={[value?.number || number]}
      options={options}
    />
  );
};

export default function Walkin({
  tableInfo = null,
  listenChange,
  setListenChange,
  setVisibleBooking,
  visibleBooking,
  setData,
  data = null,
  getReservation,
  setReservations,
  setLoadingContent,
  id,
  isWalkin
}) {
  const formRef = useRef();
  const tab = new URLSearchParams(window.location.search).get("tab");
  const AuthSelector = useSelector(authUser);
  const dispatch = useDispatch();
  const [form] = Form.useForm();

  const [visibleDate, setVisibleDate] = useState(false);
  const [loading, setLoading] = useState(false);
  const [visibleTable, setVisibleTable] = useState(false);
  const [visibleTime, setVisibleTime] = useState(false);

  const checkType = (_, value) => {
    if (value?.number[0]) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Table Type is required!"));
  };

  const checkDate = (_, value) => {
    if (value?.number) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Date is required!"));
  };

  const checkTime = (_, value) => {
    if (value?.number) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Time is required!"));
  };

  const onFinish = (e: any) => {

    if( parseInt(e?.male) + parseInt(e?.female) + parseInt(e?.kids) !== parseInt(e?.no_of_occupancy?.number)){
      Toast.show({
        icon: "fail",
        content: 'No Of People and male, female or kids count is not equal!',
      });
      return;
    }

    let hrs = e.time.number;
    let date = e.date.number;

    if (hrs.split(" ")[1] === "PM") {
      hrs =
        12 +
        parseInt(hrs.split(" ")[0]) +
        ":" +
        hrs.split(":")[1].replace(" PM", "") +
        ":00";
    } else {
      hrs = hrs.split(" ")[0] + ":00";
    }

    if (
      new Date(
        date.split("-")[1] +
          "-" +
          date.split("-")[0] +
          "-" +
          date.split("-")[2] +
          " " +
          hrs
      ) < new Date()
    ) {
      notification["error"]({
        message: "Date and time cannot be less than current time",
      });
      return;
    }

    if (e.status === 4 && !data?.table?.length) {
      notification["error"]({ message: "Please Assign Table First." });
      return;
    }

    setLoading(true);

    if (!tableInfo) setLoadingContent(true);

    let formData = new FormData();

    for (const [key, valueObj] of Object.entries(e)) {
      let value: any = "";
      value = valueObj;

      if (typeof value !== "undefined" && value !== null) {
        if (
          ["date", "time", "table_type_id", "no_of_occupancy"].includes(key)
        ) {
          formData.append(key, value?.number);
        } else {
          formData.append(key, value);
        }
      }
    }

    if(isWalkin)
      formData.append("is_walkin", "1");

    filledReservation(formData, data?.id)
      .then((res: any) => {
        formRef.current?.resetFields();
        Toast.show({
          icon: "Success",
          content: res.message,
        });

        if (data?.id) {
          dispatch(setAuthUser({ ...AuthSelector, restaurant: [res.data] }));
        }

        if (tableInfo) {
          setVisibleBooking(false);
          setLoading(false);
          return;
        }

        setVisibleBooking(false);

        if (listenChange) {
          setListenChange(listenChange + 1);
          setLoading(false);
          return;
        }

        if (tab === "1") {
          getReservation(id, 1, "today").then((res) => {
            setReservations(res);
            setLoadingContent(false);
            setLoading(false);
          });

          return;
        }

        if (tab === "2") {
          getReservation(id, 1, "tomorrow").then((res) => {
            setReservations(res);
            setLoadingContent(false);
            setLoading(false);
          });

          return;
        }

        getReservation(id, 1, date, "is_day").then((res) => {
          setReservations(res);
          setLoadingContent(false);
          setLoading(false);
        });
      })
      .catch((err: any) => {
        Object.entries(err?.response?.data?.errors)?.map((res: any) => {
          Toast.show({
            icon: "fail",
            content: res[1][0],
          });
        });
        setLoading(false);
      });
  };

  let status = [];
  if((!data && isWalkin) || data?.is_walkin){
    status = [
      { label: "Pending", value: 0 },
      { label: "Waitlisted", value: 1 },
      { label: "Confirmed", value: 2 },
      { label: "Partial Seated", value: 3 },
      { label: "Seated", value: 4 },
    ];
  
  }else{
    status = [
      { label: "Confirmed", value: 2 },
      { label: "Waitlisted", value: 1 }
    ];  
  }

  const seatsValidator = (_, value) => {
    if (value?.number) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Seat is required!"));
  };

  const checkPhone = (_, value) => {
    if (isNaN(value)) {
      return Promise.reject(new Error("Contact Number Must be in Number!"));
    }

    if (value.toString().length !== 10) {
      return Promise.reject(new Error("Contact Number must be of 10 digits!"));
    }

    return Promise.resolve();
  };

  function checkout() {
    if (tableInfo) {
      checkoutOne(tableInfo?.id, data?.id).then((res) => {
        dispatch(
          setAuthUser({ ...AuthSelector, restaurant: [res?.data?.restaurant] })
        );
        setVisibleBooking(false);
        Toast.show({
          icon: "Success",
          content:
            res.message +
            "! Table [" +
            res?.data.rmTable.map((res) => res.title).join(",") +
            "] is Empty.",
        });
      });
    }
  }

  var today = new Date();
  const [searchLoading, setSearchLoading] = useState(false);

  function searchUser(e){
    setSearchLoading(true);
    if(e.target.value.length === 10) {
      searchUserFn(e.target.value)
      .then(res => {
        if(res?.name){
          form.setFieldsValue({
            'name' : res.name,
            'email' : res.email,
          })
        }
        setSearchLoading(false);
      })
    }
  }

  // get User by restaurant 
  const [users, setUsers] = useState([]);

  useEffect(() => {
    getUsers(1)
    .then(res => {
      let user = [];
      res.map(resp => {
        user.push({label: resp.name, value: resp.id})
      })

      setUsers(user);
    })

    form.resetFields();
  }, [visibleBooking])

  return (
    <Modal
      footer={null}
      width={900}
      destroyOnClose={true}
      visible={visibleBooking}
      className="booking"
      onCancel={() => setVisibleBooking(false)}
    >
      <div className="col-md-12 body-dark py-4 px-4 px-md-5">
        <Form
          onFinish={onFinish}
          autoComplete="off"
          form={form}
          ref={formRef}
          layout="vertical"
        >
          <div className="row d-flex justify-content-between pt-2">
            <div className="col-md-6">

              <h2 className="light">{(isWalkin || data?.is_walkin) ? ( data ? 'Update Walkin Booking' : 'Add Walkin Booking' ) : (data ? 'Update Reservation' : 'Add Reservation')}</h2>
              <p className="faded">Hi, Good Afternoon,Check your Stats Today</p>
            </div>
            <div className="col-md-3 my-auto d-flex justify-content-end">
              {tableInfo && (
                <Button
                  className="mr-3 btn-red"
                  onClick={checkout}
                  htmlType="button"
                  block
                >
                  Checkout
                </Button>
              )}

              <Button
                className="btn-green mt-3 mt-md-0"
                loading={loading}
                htmlType="submit"
                block
              >
                {data ? "Update" : "Save"}
              </Button>
            </div>
          </div>

          <section className="row mt-4">
            <div className="col-md-12">
              <Form.Item
                  label="Contact No"
                  hasFeedback={searchLoading}
                  validateStatus="validating"
                  initialValue={data?.phone || null}
                  name="phone"
                  rules={[
                    {
                      required: true,
                      message: "Contact No is required",
                    },
                    {
                      validator: checkPhone,
                    },
                  ]}
                >
                <Input onKeyUp={(e) => searchUser(e)} placeholder="Contact No Here" />
              </Form.Item>

              <Form.Item
                label="Reservation Status"
                name="status"
                initialValue={data?.status ? [data?.status] : ""}
                rules={[
                  {
                    required: true,
                    message: "Reservation Status is required",
                  },
                ]}
              >
                <Select placeholder="Select Here" options={status} />
              </Form.Item>

              <Form.Item
                name="no_of_occupancy"
                label="How Many People?"
                rules={[
                  {
                    validator: seatsValidator,
                  },
                ]}
              >
                <Occupancy initialValue={data?.no_of_occupancy?.toString()} />
              </Form.Item>

              <div className="row">
                <div className="col-4 pe-1">
                  <Form.Item
                    label="Male"
                    name="male"
                    initialValue={data?.male || 0}
                    rules={[
                      {
                        required: true,
                        message: "Please input No of Male!",
                      },
                    ]}
                  >
                    <Input
                      placeholder="No"
                      size="large"
                      type="number"
                      min={"0"}
                    />
                  </Form.Item>
                </div>
                <div className="col-4 pe-1">
                  <Form.Item
                    initialValue={data?.female || 0}
                    label="Female"
                    name="female"
                    rules={[
                      {
                        required: true,
                        message: "Please Enter No of Female!",
                      },
                    ]}
                  >
                    <Input
                      placeholder="No"
                      size="large"
                      type="number"
                      min={"0"}
                    />
                  </Form.Item>
                </div>
                <div className="col-4 pe-1">
                  <Form.Item
                    label="Kids"
                    initialValue={data?.kids || 0}
                    name="kids"
                    rules={[
                      {
                        required: true,
                        message: "Please input No of Kids!",
                      },
                    ]}
                  >
                    <Input
                      placeholder="No"
                      size="large"
                      type="number"
                      min={"0"}
                    />
                  </Form.Item>
                </div>
              </div>
                

              <Form.Item
                label="Name"
                name="name"
                initialValue={data?.name || null}
                rules={[
                  {
                    required: true,
                    message: "Name is required!",
                  },
                ]}
              >
                <Input placeholder="Name Here" />
              </Form.Item>

              <Form.Item
                label="Email Address"
                initialValue={data?.email || null}
                name="email"
                shouldUpdate
                rules={[
                  {
                    required: true,
                    message: "Email Address is required",
                  },
                  {
                    type: "email",
                    message: "Not a valid email",
                  },
                ]}
              >
                <Input placeholder="Enter Email Address" />
              </Form.Item>

              <div className="row">
                <div className="col-md-6">
                  <Form.Item
                    label="Date"
                    name="date"
                    className="w-100"
                    rules={[{ validator: checkDate }]}
                    onClick={() => setVisibleDate(true)}
                  >
                    <DatePickerComp
                      initialValue={[1 ,2].includes(isWalkin) ? moment(today) : data?.date}
                      setVisible={setVisibleDate}
                      suffix={false}
                      visible={visibleDate}
                    />
                  </Form.Item>
                </div>

                <div className="col-md-6">
                  <Form.Item
                    label="Time"
                    name="time"
                    className="w-100"
                    onClick={() => setVisibleTime(true)}
                    rules={[{ validator: checkTime }]}
                  >
                    <TimePicker
                      initialValue={[1 ,2].includes(isWalkin) ? moment(today).format('hh:mm A') : data?.time}
                      setVisible={setVisibleTime}
                      suffix={false}
                      visible={visibleTime}
                    />
                  </Form.Item>
                </div>
              </div>

              <Form.Item
                label="Seat Options"
                name={"table_type_id"}
                className="w-100"
                onClick={() => setVisibleTable(true)}
                rules={[
                  {
                    validator: checkType,
                  },
                ]}
              >
                <TableType
                  initialValue={data?.table_type_id}
                  setVisible={setVisibleTable}
                  visible={visibleTable}
                  suffix={false}
                />
              </Form.Item>

              <Form.Item
                  label="Reserved By"
                  name={"reserved_by"}
                  className="w-100"
                  initialValue={data?.reserved_by ? [data?.reserved_by] : null}
                >
                  <Select placeholder="Select Here" options={users} />
              </Form.Item>

              { (data?.is_walkin !== 1 || (!data && !isWalkin)) && 
                <Form.Item
                  label="Source of Reservation "
                  name={"source"}
                  className="w-100"
                  initialValue={data?.source ? [data?.source] : null}
                >
                  <Select placeholder="Select Here" options={[{ value: 'Zomato'}, { value: 'Online'}, {value: 'Personal'}, {value: 'Official'}]} />
                </Form.Item>
              }
            </div>
          </section>
        </Form>
      </div>
    </Modal>
  );
}
