import userApi from "@/services/userApi";
import userAuth from "@/services/userAuth";

export const tableType = (id: any) => {
    return userApi
    .get(`table-types/${id}`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const filledReservation = (formData: any, id='') => {
    return userApi
    .post(`filled-reservation${id ? '/'+id : ''}`, formData)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const saveToken = (token: any) => {
    return userAuth
    .post(`token-save`, {
        device: 'any',
        token: token
    })
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const getReservation = (email: any) => {
    return userApi
    .get(`get-reservation/${email}`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const checkoutOne = (id: number, user_id:number) => {
    return userAuth
    .get(`checkout-one/${id}/${user_id}`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const searchUserFn = (number:number) => {
    return userAuth
    .post(`search-user-phone`, {
        phone : number
    })
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const getUsers = (id:number) => {
    return userAuth
    .get(`get-users/${id}`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};
