import userAuth from "@/services/userAuth";
import userApi from "@/services/userApi";

export const login = (formData: any) => {
    return userApi
    .post('/login', formData)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const statusChange = (formData: any) => {
    return userAuth
    .post('/status', formData)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const user = () => {
    return userAuth
    .get('/user')
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const logout = () => {
    return userAuth
    .get(`logout`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const checkout = (id) => {
    return userAuth
    .post(`checkout/${id}`, {})
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};

export const deleteReservation = (id) => {
    return userAuth
    .delete(`delete/${id}`, {})
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
}

export const getReservation = (id:number, page=1, day='today', status='', search='', waitFrom='', waitTo='') => {
    return userAuth
    .get(`/reservation/${id}?page=${page}&day=${day}&status=${status}&filter=${search}&wait_from=${waitFrom}&wait_to=${waitTo}`)
    .then((res: any) => res.data)
    .catch((err: any) => {throw err});
};


