import React, { useEffect, useState } from "react";
import { Button, Tabs, Dropdown, Menu, Skeleton, Empty } from "antd";
import { DownOutlined } from "@ant-design/icons";
import FreeTable from "@/assets/free-table.svg";
import OccupiedTable from "@/assets/occupied-table.svg";
import FreeTable6 from "@/assets/6 Tables - Green.svg";
import OccupiedTable6 from "@/assets/6 Tables -Red.svg";
import FreeTable2 from "@/assets/2-Chair-Green.svg";
import OccupiedTable2 from "@/assets/2-Chair-red.svg";
import Sidebar from "@/parts/restaurant/sidebar";
import { restaurant } from "@/reducers/auth";
import { useSelector } from "react-redux";
import Walkin from "@/components/Restaurant/walkinModal";

function Tables() {
  const restaurantSelector = useSelector(restaurant);
  const [loader, setLoader] = useState(true);
  const [visibleBooking, setVisibleBooking] = useState(false);
  const [data, setData] = useState(null);
  const [tableInfo, setTableInfo] = useState(null);
  const [floor, setFloor] = useState(null);

  useEffect(() => {
    if (restaurantSelector) {
      setFloor(restaurantSelector?.floor);
      setLoader(false);
    }
  }, [restaurantSelector]);

  // START Function for Ant Tabs
  const { TabPane } = Tabs;

  function callback(key) {
    setFloor(restaurantSelector?.floor);
  }
  // END Function for Ant Tabs

  function handleMenuClick(e) {
    if (e.key === "All") {
      setFloor(restaurantSelector?.floor);
      return;
    }
    setFloor(restaurantSelector.floor.filter((res) => res.title === e.key));
  }

  const menu = (
    <Menu onClick={handleMenuClick}>
      <Menu.Item key={"All"}>All</Menu.Item>
      {restaurantSelector.floor.map((res, key) => (
        <Menu.Item key={res.title}>{res.title}</Menu.Item>
      ))}
    </Menu>
  );

  function showDetail(table) {
    setData(table.user);
    setTableInfo(table);
    setVisibleBooking(true);
  }

  return (
    <>
      <section className="container-fluid body-dark py-4 px-4">
        <section className="row">
          <Sidebar />

          <div className="col-md-10  table-management">
            <>
              <div className="d-flex justify-content-between">
                <div className="col-md-6 p-0">
                  <h2 className="light">Table Mangement</h2>
                  <p className="faded">
                    Hi, Good Afternoon,Check your Stats Today
                  </p>
                </div>
                <div className="col-md-3 p-0 my-auto d-flex justify-content-end">
                  <Dropdown overlay={menu}>
                    <Button>
                      All Section <DownOutlined />
                    </Button>
                  </Dropdown>
                </div>
              </div>
              {loader ? (
                <Skeleton active />
              ) : (
                <section className="my-4">
                  <div className="">
                    <Tabs defaultActiveKey="0" onChange={callback}>
                      <TabPane tab="All" key="0">
                        {floor.map((res: any, key: key) => (
                          <React.Fragment key={key}>
                            <div className="mb-3 col-md-12 mx-n3">
                              <h4 className="text-white">{res.title}</h4>

                              <div className="row">
                                {res.table.map((table: any, key: key) => (
                                  <React.Fragment key={key}>
                                    <div
                                      className="col-md-3 col-6 col-sm-6 col-lg-2 cursor"
                                      onClick={() => {
                                        table.status === 1 && showDetail(table);
                                      }}
                                    >
                                      <span className="position-relative cursor">
                                        <label
                                          htmlFor={`myCheckbox${table?.id}`}
                                        >
                                          {table.status === 1 ? (
                                            <img
                                              src={table?.no_of_occupany === 2 ? OccupiedTable2 : table?.no_of_occupany === 4 ? OccupiedTable : OccupiedTable6}
                                              alt="..."
                                              width={"170px"}
                                            />
                                          ) : (
                                            <img
                                              src={table?.no_of_occupany === 2 ? FreeTable2 : table?.no_of_occupany === 4 ? FreeTable : FreeTable6}
                                              alt="..."
                                              width={"170px"}
                                            />
                                          )}

                                          <div className="position-absolute seat-text text-center lh-1">
                                            <p className="light text-center mb-1 text-uppercase">
                                              {table?.title}
                                            </p>
                                            <p className="light font-bold mb-0">
                                              {table?.no_of_occupany} Seats
                                            </p>
                                            <small className="light">
                                              ({table?.table_type?.title})
                                            </small>
                                          </div>
                                        </label>
                                      </span>
                                    </div>
                                  </React.Fragment>
                                ))}
                              </div>
                              <div className="pb-5">
                                {!res.table.length && (
                                  <div className="bg-white card py-4">
                                    <Empty description="No Table Added!" />
                                  </div>
                                )}
                              </div>
                            </div>
                          </React.Fragment>
                        ))}
                        {/* {
                        !floor.lenght && <div className="card py-4 text-white"> <Empty description={"No Table Added"}/></div>
                      } */}
                      </TabPane>

                      <TabPane tab="Free" key="1">
                        <div>
                          {floor.map((res: any, key: key) => (
                            <React.Fragment key={key}>
                              <h4 className="text-white">{res.title}</h4>
                              <div className="col-md-12 mb-3 mx-n3">
                                <div className="row">
                                  {res.table
                                    .filter((res) => res.status === 0)
                                    .map((res: any, key: key) => (
                                      <React.Fragment key={key}>
                                        <div className="col-md-3 col-6 col-sm-6 col-lg-2 cursor">
                                          <span className="position-relative">
                                            <img
                                              src={res?.no_of_occupany === 2 ? FreeTable2 : res?.no_of_occupany === 4 ? FreeTable : FreeTable6}
                                              alt="..."
                                              width={"170px"}
                                            />
                                            <div className="position-absolute seat-text text-center lh-1">
                                              <p className="light text-center mb-1 text-uppercase">
                                                {res?.title}
                                              </p>
                                              <p className="light font-bold mb-0">
                                                {res?.no_of_occupany} Seats
                                              </p>
                                              <small className="light">
                                                ({res?.table_type?.title})
                                              </small>
                                            </div>
                                          </span>
                                        </div>
                                      </React.Fragment>
                                    ))}

                                  {res.table.filter((res) => res.status === 0)
                                    .length === 0 && (
                                    <div className="card bg-white py-3">
                                      <Empty
                                        description={
                                          <div>No table Available!</div>
                                        }
                                      />
                                    </div>
                                  )}
                                </div>
                              </div>
                            </React.Fragment>
                          ))}

                          {/* {
                              !floor.lenght && <div className="card py-4 text-white"> <Empty description={"No Table Added"}/></div>
                            } */}
                        </div>
                      </TabPane>

                      <TabPane tab="Occupied" key="2">
                        <div>
                          {floor.map((res: any, key: key) => (
                            <React.Fragment key={key}>
                              <h4 className="text-white">{res.title}</h4>
                              <div className="col-md-12 mb-3 mx-n3">
                                <div className="row mb-4">
                                  {res.table
                                    .filter((res) => res.status === 1)
                                    .map((res: any, key: key) => (
                                      <React.Fragment key={key}>
                                        <div
                                          className="col-md-3 col-6 col-sm-6 col-lg-2 cursor"
                                          onClick={() => {
                                            showDetail(res);
                                          }}
                                        >
                                          <span className="position-relative cursor">
                                            <span className="position-relative">
                                              <img
                                                src={res?.no_of_occupany === 2 ? OccupiedTable2 : res?.no_of_occupany === 4 ? OccupiedTable : OccupiedTable6}
                                                alt="..."
                                                width={"170px"}
                                              />
                                              <div className="position-absolute seat-text text-center lh-1">
                                                <p className="light text-center mb-1">
                                                  {res?.title}
                                                </p>
                                                <p className="light font-bold mb-0">
                                                  {res?.no_of_occupany} Seats
                                                </p>
                                                <small className="light font-bold">
                                                  {res?.table_type?.title}
                                                </small>
                                              </div>
                                            </span>
                                          </span>
                                        </div>
                                      </React.Fragment>
                                    ))}

                                  <div className="mb-5 px-3">
                                    {res.table.filter((res) => res.status === 1)
                                      .length === 0 && (
                                      <div className="card bg-white py-3">
                                        <Empty
                                          description={
                                            <div>No table Occupied!</div>
                                          }
                                        />
                                      </div>
                                    )}
                                  </div>
                                </div>
                              </div>
                            </React.Fragment>
                          ))}
                        </div>
                      </TabPane>
                    </Tabs>
                  </div>
                </section>
              )}
            </>
          </div>
        </section>
      </section>

      {/* Booking  */}
      <Walkin
        data={data}
        tableInfo={tableInfo}
        setVisibleBooking={setVisibleBooking}
        visibleBooking={visibleBooking}
      />
    </>
  );
}

export default Tables;
