import { useEffect, useState } from "react";
import { Picker } from "antd-mobile";
import { ClockCircleOutlined } from '@ant-design/icons'
import { Input } from "antd";
import moment from "moment";

export default function TimePicker ({ initialValue, value= {}, onChange, setVisible, visible, suffix=true}: any) {
    const [number, setNumber] = useState(initialValue ? initialValue : '');

    const triggerChange = (changedValue) => {
      onChange?.({
        number,
        ...value,
        ...changedValue,
      });
    };

    useEffect(() => {
      if(initialValue) {
        triggerChange(initialValue);
      }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [initialValue])
  
    const onNumberChange = (e) => {
      const newNumber = `${e[0]}:${e[1]} ${e[2]}`;
      if (!('number' in value)) {
        setNumber(`${e[0]}:${e[1]} ${e[2]}`);
      }

      triggerChange({
        number: newNumber,
      });
    };
  
    let hrs: any = [];  
    let mins: any = [];
  
    [...Array(13).keys()].map((res: any) => {
      res = res * 5;
      mins.push( res < 10 ? '0'+res.toString() : res.toString());
    });
  
    [...Array(13).keys()].map((res: any) => {
      if(res > 0){
        hrs.push(res.toString());
      }
    })
    
    const basicColumns = [
      hrs,mins,
      ['AM', 'PM']
    ];

    // const [visible, setVisible] = useState(false);
  
    return (
      <span>
        <Picker 
          title="Booking Time"
          confirmText="Confirm"
          cancelText="Cancel"
          onClose={() => setVisible(false)}
          columns={basicColumns} 
          visible={visible}
          onConfirm={(e) => {
            onNumberChange(e);
            setNumber(moment(new Date()).format('h') + ':' + moment(new Date()).format('mm') +' '+ moment(new Date()).format('A'))
          }}
          defaultValue={[moment(new Date()).format('h'), (moment(new Date()).format('m') < 10) ? 0+moment(new Date()).format('m'): moment(new Date()).format('m'), moment(new Date()).format('A')]}
          onSelect={onNumberChange}
          />

          <Input
            type="text"
            placeholder="Select Time"
            value={value?.number || number}
            disabled
            suffix={ suffix && <ClockCircleOutlined/>}
          />
      </span>
    );
};