import { Button, Tabs, message, Modal, Skeleton, Empty } from "antd";
import React, { useEffect, useRef, useState } from "react";
import FreeTable from "@/assets/free-table.svg";
import { statusChange } from "@/api/restaurant/index";
import OccupiedTable from "@/assets/occupied-table.svg";
import { useDispatch, useSelector } from "react-redux";
import { restaurant } from "@/reducers/auth";
import { Dialog } from "antd-mobile";
import Tick from "@/assets/tick.svg";
import { setAuthUser, authUser } from "@/reducers/auth";

export default function TableModal({
  listenChange,
  setListenChange,
  visible,
  setVisible,
  data = null,
  getReservation,
  setReservations,
  setLoadingContent,
  id,
  waitlisted = false,
}) {
  const date = new URLSearchParams(window.location.search).get("date");
  const tab = new URLSearchParams(window.location.search).get("tab");
  const restaurantSelector = useSelector(restaurant);
  const authSelector = useSelector(authUser);
  const [loader, setLoader] = useState(true);
  const formRef = useRef();
  const [totalTable, setTableSelected] = useState(0);
  const [selectedTable, setSelectedTable] = useState([]);
  const dispatch = useDispatch();

  useEffect(() => {
    if (restaurantSelector) {
      setLoader(false);
    }
  }, [restaurantSelector]);

  const { TabPane } = Tabs;

  const [isModalVisible, setIsModalVisible] = useState(false);

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  function callback(key) {
    // console.log(key);
  }

  useEffect(() => {}, [authSelector]);

  const changeStatus = (e: any) => {
    e.preventDefault();

    if (selectedTable.length === 0) {
      return message.error("No table Selected, Please select table.");
    }

    Dialog.confirm({
      content: "Are you sure to assign these table?",
      cancelText: <div className="text-danger">Cancel</div>,
      confirmText: "Ok",
      onConfirm: () => {
        let formData = new FormData(e.target);
        formData.append("user_id", data?.id);
        selectedTable.map((res) => {
          formData.append("table[]", res);
        });
        formData.append("status", "1");

        statusChange(formData).then((res: any) => {
          setVisible(false);
          setIsModalVisible(true);

          dispatch(setAuthUser({ ...authSelector, restaurant: [res.data] }));

          if (listenChange) {
            setListenChange(listenChange + 1);
            setLoadingContent(false);
            return;
          }

          if (waitlisted) {
            getReservation(id, 1, "waitlisted").then((res) => {
              setReservations(res);
              setLoadingContent(false);
            });
            return;
          }

          if (tab === "1") {
            getReservation(id, 1, "today").then((res) => {
              setReservations(res);
              setLoadingContent(false);
            });
            return;
          }

          if (tab === "2") {
            getReservation(id, 1, "tomorrow").then((res) => {
              setReservations(res);
              setLoadingContent(false);
            });

            return;
          }

          getReservation(id, 1, date, "is_day").then((res) => {
            setReservations(res);
            setLoadingContent(false);
          });
        });
      },
      onCancel: () => {
        return;
      },
    });
  };

  return (
    <>
      <Modal visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <div className="d-flex text-center flex-column">
          <img src={Tick} alt="..." width={"100px"} className="mx-auto mb-3" />
          <p className="light font-bold f-20">Booking Confirmed</p>
          <p className="faded">
            Your Reservation status has been recored to the resturnt.
          </p>
        </div>
      </Modal>
      <Modal
        visible={visible}
        onCancel={() => setVisible(false)}
        width={1200}
        footer={null}
        className="booking"
      >
        <div className="col-md-12 body-dark py-4 px-5">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              changeStatus(e);
            }}
            ref={formRef}
          >
            <div className="col-md-12 d-flex pt-2">
              <div className="lh-1">
                <p className="text-warning f-16">
                  {data?.no_of_occupancy} Seats Required | {totalTable} Seats
                  Selected
                </p>
                <small className="text-danger">
                  {parseInt(data?.no_of_occupancy) === parseInt(totalTable)
                    ? "Great!! Seat matched requirement"
                    : parseInt(data?.no_of_occupancy) < parseInt(totalTable)
                    ? "More seats selected, Add less table!!"
                    : "Great!!!, Partial seat added"}
                </small>
              </div>
              <div className="col-md-3 ml-auto my-auto d-flex justify-content-end">
                <Button htmlType="submit" className="btn-green" block>
                  Assign Table
                </Button>
              </div>
            </div>
            <section className="row">
              <div className="col-md-12 px-4">
                {loader ? (
                  <Skeleton active />
                ) : (
                  <section className="row">
                    <div className="col-md-12">
                      <Tabs defaultActiveKey="0" onChange={callback}>
                        <TabPane tab="All" key="0">
                          {restaurantSelector?.floor?.map(
                            (res: any, key: key) => (
                              <React.Fragment key={key}>
                                <div className="mb-3">
                                  <h4 className="text-white">{res.title}</h4>

                                  <div className="row">
                                    {res.table.map((table: any, key: key) => (
                                      <React.Fragment key={key}>
                                        <div className="col-md-3 col-lg-2 col-6 col-sm-4 cursor">
                                          <span className="position-relative cursor table-box">
                                            <input
                                              type="checkbox"
                                              checked={
                                                selectedTable.includes(
                                                  table?.id
                                                )
                                                  ? true
                                                  : false
                                              }
                                              disabled={
                                                table.status === 0
                                                  ? false
                                                  : true
                                              }
                                              value={table?.id}
                                              id={`myCheckbox${table?.id}`}
                                              onChange={(e) => {
                                                if (e.target.checked) {
                                                  setSelectedTable([
                                                    ...selectedTable,
                                                    table?.id,
                                                  ]);
                                                  setTableSelected(
                                                    totalTable +
                                                      table?.no_of_occupany
                                                  );
                                                } else {
                                                  setSelectedTable(
                                                    (selected: any) => {
                                                      let index =
                                                        selected.indexOf(
                                                          table?.id
                                                        );
                                                      if (index > -1) {
                                                        selected.splice(
                                                          index,
                                                          1
                                                        );
                                                      }
                                                      return selected;
                                                    }
                                                  );
                                                  setTableSelected(
                                                    totalTable -
                                                      table?.no_of_occupany
                                                  );
                                                }
                                              }}
                                            />

                                            <label
                                              htmlFor={`myCheckbox${table?.id}`}
                                            >
                                              {table.status === 1 ? (
                                                <img
                                                  src={OccupiedTable}
                                                  alt="..."
                                                  width={"170px"}
                                                />
                                              ) : (
                                                <img
                                                  src={FreeTable}
                                                  alt="..."
                                                  width={"170px"}
                                                />
                                              )}

                                              <div className="position-absolute seat-text text-center lh-1">
                                                <p className="light text-center mb-1 text-uppercase">
                                                  {table?.title}
                                                </p>
                                                <p className="light font-bold mb-0">
                                                  {table?.no_of_occupany} Seats
                                                </p>
                                                <small className="light">
                                                  ({table?.table_type?.title})
                                                </small>
                                              </div>
                                            </label>
                                          </span>
                                        </div>
                                      </React.Fragment>
                                    ))}
                                  </div>
                                  {!res.table.length && (
                                    <div className="bg-white card py-4">
                                      <Empty description="No Table Available!" />
                                    </div>
                                  )}
                                </div>
                              </React.Fragment>
                            )
                          )}
                        </TabPane>

                        <TabPane tab="Free" key="1">
                          {restaurantSelector?.floor?.map(
                            (res: any, key: key) => (
                              <React.Fragment key={key}>
                                <h4 className="text-white">{res.title}</h4>

                                <div className="row">
                                  {res.table
                                    .filter((res) => res.status === 0)
                                    .map((table: any, key: key) => (
                                      <React.Fragment key={key}>
                                        <div className="col-md-3 col-lg-2 col-6 col-sm-4 cursor">
                                          <span className="position-relative cursor table-box">
                                            <input
                                              type="checkbox"
                                              disabled={
                                                table.status === 0
                                                  ? false
                                                  : true
                                              }
                                              checked={
                                                selectedTable.includes(
                                                  table?.id
                                                )
                                                  ? true
                                                  : false
                                              }
                                              value={table?.id}
                                              id={`myCheckbox${table?.id}`}
                                              onChange={(e) => {
                                                if (e.target.checked) {
                                                  setSelectedTable([
                                                    ...selectedTable,
                                                    table?.id,
                                                  ]);
                                                  setTableSelected(
                                                    totalTable +
                                                      table?.no_of_occupany
                                                  );
                                                } else {
                                                  setSelectedTable(
                                                    (selected: any) => {
                                                      let index =
                                                        selected.indexOf(
                                                          table?.id
                                                        );
                                                      if (index > -1) {
                                                        selected.splice(
                                                          index,
                                                          1
                                                        );
                                                      }
                                                      return selected;
                                                    }
                                                  );
                                                  setTableSelected(
                                                    totalTable -
                                                      table?.no_of_occupany
                                                  );
                                                }
                                              }}
                                            />
                                            <label
                                              htmlFor={`myCheckbox${table?.id}`}
                                            >
                                              <img
                                                src={FreeTable}
                                                alt="..."
                                                width={"170px"}
                                              />
                                              <div className="position-absolute seat-text text-center lh-1">
                                                <p className="light text-center mb-1 text-uppercase">
                                                  {table?.title}
                                                </p>
                                                <p className="light font-bold mb-0">
                                                  {table?.no_of_occupany} Seats
                                                </p>
                                                <small className="light">
                                                  ({table?.table_type?.title})
                                                </small>
                                              </div>
                                            </label>
                                          </span>
                                        </div>
                                      </React.Fragment>
                                    ))}
                                </div>
                                {!res.table.filter((res) => res.status === 0)
                                  .length && (
                                  <div className="bg-white card py-4 mb-4">
                                    <Empty description="No Table Available!" />
                                  </div>
                                )}
                              </React.Fragment>
                            )
                          )}
                        </TabPane>

                        {/* <TabPane tab="Partially Seated" key="3">
                                <div>
                                    {
                                    restaurantSelector?.floor.map((res:any, key: key) => (
                                        <React.Fragment key={key}>
                                        {
                                            res.table.filter(res => res.status === 2).length > 0 && <h4 className="text-white">{ res.title }</h4>
                                            }

                                        <div className="d-flex flex-wrap mb-4">
                                            {
                                                res.table.filter(res => res.status === 2).map((table:any, key:key) => (
                                                <React.Fragment key={key}>
                                                    <div className="col-2 cursor">
                                                        <span className="position-relative cursor table-box">
                                                            <input type="checkbox" name="table[]" value={table?.id} id={`myCheckbox${table?.id}`} onChange={(e) => {
                                                                if(e.target.checked){
                                                                    setTableSelected(totalTable + table?.no_of_occupany)
                                                                }else{
                                                                    setTableSelected(totalTable - table?.no_of_occupany)
                                                                }
                                                            }} />
                                                            <label htmlFor={`myCheckbox${table?.id}`}>
                                                                {partially}
                                                                
                                                                <div className="position-absolute seat-text text-center lh-1">
                                                                    <p className="light text-center mb-1 text-uppercase">{ table?.title }</p>
                                                                    <p className="light font-bold mb-0">{table?.no_of_occupany} Seats</p>
                                                                    <small className="light">({table?.table_type?.title})</small>                                               
                                                                </div>
                                                            </label>
                                                        </span>
                                                    </div>
                                                </React.Fragment>
                                                ))
                                            }
                                        </div>
                                        </React.Fragment>
                                    ))
                                    }
                                </div>
                            </TabPane> */}

                        <TabPane tab="Occupied" key="2">
                          <div>
                            {restaurantSelector?.floor?.map(
                              (res: any, key: key) => (
                                <React.Fragment key={key}>
                                  {res.table.filter((res) => res.status === 1)
                                    .length > 0 && (
                                    <h4 className="text-white">{res.title}</h4>
                                  )}

                                  <div className="d-flex flex-wrap mb-4">
                                    {res.table
                                      .filter((res) => res.status === 1)
                                      .map((res: any, key: key) => (
                                        <React.Fragment key={key}>
                                          <span className="position-relative">
                                            <img
                                              src={OccupiedTable}
                                              alt="..."
                                              width={"170px"}
                                            />
                                            <div className="position-absolute seat-text text-center lh-1">
                                              <p className="light text-center mb-1">
                                                {res?.title}
                                              </p>
                                              <p className="light font-bold mb-0">
                                                {res?.no_of_occupany} Seats
                                              </p>
                                              <small className="light font-bold">
                                                {res?.table_type?.title}
                                              </small>
                                            </div>
                                          </span>
                                        </React.Fragment>
                                      ))}
                                  </div>
                                </React.Fragment>
                              )
                            )}
                          </div>
                        </TabPane>
                      </Tabs>
                    </div>
                  </section>
                )}
              </div>
            </section>
          </form>
        </div>
      </Modal>
    </>
  );
}
