import { useEffect, useState } from "react";
import { Picker } from "antd-mobile";
import { Input } from "antd"
import { BorderOuterOutlined } from '@ant-design/icons'

const type  = [   
  {label: 'Non Smoking', value: '1'},
  {label: 'Smoking', value: '2'}
]

export default function TableType ({initialValue, value= {}, onChange, setVisible, visible, suffix=true}) {
    const [number, setNumber] = useState('');
    const [label, setLabel] = useState('');

    useEffect(() => {
      if(initialValue) {
        let typeNow = type.filter(res => res.value === initialValue.toString());

        setLabel(typeNow[0].label);
        setNumber(typeNow[0].value);

        triggerChange({ number: typeNow[0].value });
      }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [initialValue]);

    const triggerChange = (changedValue) => {
      onChange?.({
        number,
        ...value,
        ...changedValue,
      });
    };
  
    const onNumberChange = (e) => {
  
      let typeNow = type.filter(res => res.value === e[0].toString());
  
      const newNumber = e;
      if (!('number' in value)) {
        setNumber(e);
      }
  
      if (!('label' in value)) {
        setLabel(typeNow[0].label);
      }
  
      triggerChange({
        number: newNumber,
      });
      
    };

    return (
      <span>
        <Picker
          columns={[type]}
          visible={visible}
          confirmText="Confirm"
          cancelText="Cancel"
          onClose={() => setVisible(false)}
          onSelect={(e) => {onNumberChange(e); }}
          />
          
        <Input
          type="text"
          placeholder="Select Table Type"
          value={value?.label || label}
          disabled
          suffix={suffix && <BorderOuterOutlined/>}
        />
      </span>
    );
  };