import React, { useState, useEffect } from "react";
import { Line, G2, Pie } from "@ant-design/charts";
import Sidebar from "@/parts/restaurant/sidebar";
import { each, findIndex } from "@antv/util";
import { Button, Dropdown, Menu } from "antd";
import { restaurant } from "@/reducers/auth";
