import React, { useState, useRef, useEffect } from "react";
import { Dialog, Form, TextArea, Toast } from "antd-mobile";
import { Input, Button, Modal, Result, notification } from "antd";
import { filledReservation, getReservation } from "@/api/reservation/index";
import { Selector, Stepper } from "antd-mobile";
import { DialogShowRef } from "antd-mobile/es/components/dialog";
import {
  UsergroupAddOutlined,
  UserOutlined,
  RightOutlined,
  LeftOutlined,
  PhoneOutlined,
  MailOutlined,
} from "@ant-design/icons";
import Table from "@/assets/table.jpg";
import Plate from "@/assets/plate.png";
import tick from "@/assets/tick.svg";
import ConfirmInvoice from "@/components/Reservation/confirmInvoice";
import DatePickerComp from "@/components/Reservation/DatePicker";
import TimePicker from "@/components/Reservation/TimePicker";
import TableType from "@/components/Reservation/TableType";
import { useLocation } from "react-router";
import Splash from "./Splash";

const App = () => {
  const handler = useRef<DialogShowRef>();
  const location = useLocation();
  const [seats, setSeats] = useState(0);
  const [step, setStep] = useState(1);
  const [visibleBookingModal, setVisibleModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [loadingSplash, setLoadingSplash] = useState(true);

  const [visible, setVisible] = useState(false);
  const [visibleTable, setVisibleTable] = useState(false);
  const [visibleTime, setVisibleTime] = useState(false);

  const email = new URLSearchParams(location.search).get("user");

  const [data, setData] = useState();
  const formRef = useRef();

  const onFinish = (e: any) => {
  
    if( parseInt(e.male) + parseInt(e.female) + parseInt(e.kids) !== parseInt(e?.no_of_occupancy[0])){
      Toast.show({
        icon: "fail",
        content: 'No Of People and male, female or kids count is not equal!',
      });
      return;
    }

    let hrs = e.time.number;
    let date = e.date.number;

    if (hrs.split(" ")[1] === "PM") {
      hrs =
        12 +
        parseInt(hrs.split(" ")[0]) +
        ":" +
        hrs.split(":")[1].replace(" PM", "") +
        ":00";
    } else {
      hrs = hrs.split(" ")[0] + ":00";
    }

    if (
      new Date(
        date.split("-")[1] +
          "-" +
          date.split("-")[0] +
          "-" +
          date.split("-")[2] +
          " " +
          hrs
      ) < new Date()
    ) {

      Toast.show({
        icon: "fail",
        content: 'Date and time cannot be less than current time!',
      });
      return;
    }

    if (step === 1) {
      setStep(2);
      return;
    }
    setLoading(true);

    let formData = new FormData();
    let seat: any = "";

    for (const [key, valueObj] of Object.entries(e)) {
      let value: any = "";
      value = valueObj;

      if (typeof value !== "undefined" && value !== null) {
        if (["date", "time", "table_type_id"].includes(key)) {
          formData.append(key, value?.number);
        } else {
          formData.append(key, value);
        }
      }
    }
    seat = seats;

    formData.append("no_of_occupancy", seat);

    filledReservation(formData)
      .then((res: any) => {
        formRef.current?.resetFields();
        setVisibleModal(true);
        setLoading(false);
        setData(res?.data);
        setStep(3);
      })
      .catch((err: any) => {
        console.log(err);
        Object.entries(err?.response?.data?.errors).map((res: any) => {
          Toast.show({
            icon: "fail",
            content: res[1][0],
          });
        });
        setStep(1);
        setLoading(false);
      });
  };

  useEffect(() => {
    if (email) {
      getReservation(email).then((res) => {
        setData(res);
        setStep(3);
      });
    }
  }, [email]);

  const checkManual = (e: any) => {
    if (e[0] === "plus") {
      setSeats(6);
      handler.current = Dialog.show({
        content: (
          <div>
            <label className="mb-3">Number Of Seats</label>

            <Stepper
              defaultValue={6}
              onChange={(e) => setSeats(e)}
              min={0}
              style={{
                "--border-inner": "none",
                "--button-background-color": "#F8F9FA",
                "--height": "50px",
                "--input-width": "100",
                "--active-border": "1px solid #1677ff",
              }}
            />
          </div>
        ),
        actions: [
          [
            {
              key: "save",
              text: "Ok",
              onClick: () => {
                handler.current?.close();
              },
            },
            {
              key: "close",
              text: "Cancel",
              danger: true,
              onClick: () => {
                setSeats(0);
                handler.current?.close();
              },
            },
          ],
        ],
      });
    } else {
      setSeats(e[0]);
    }
  };

  const checkTime = (_, value) => {
    if (value?.number) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Time is required!"));
  };

  const checkType = (_, value) => {
    if (value?.number[0]) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Table Type is required!"));
  };

  const checkDate = (_, value) => {
    if (value?.number) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Date is required!"));
  };

  const checkPhone = (_, value) => {
    if (!isNaN(value)) {
      return Promise.resolve();
    }

    return Promise.reject(new Error("Contact Number Must be in Number!"));
  };

  useEffect(() => {
    setTimeout(() => {
      setLoadingSplash(false);
    }, 300);
  }, []);

  if (loadingSplash) return <Splash />;

  if (step === 3) {
    return (
      <>
        <Modal
          onCancel={() => setVisibleModal(false)}
          width={400}
          visible={visibleBookingModal}
          footer={""}
        >
          <div>
            <Result
              className="p-2"
              icon={<img src={tick} alt="" width="90" />}
              status="success"
              title="Booking Submitted"
              subTitle="Your Reservation status has been recorded, We will send you mail shortly."
              extra={[
                <Button
                  key={1}
                  onClick={() => setVisibleModal(false)}
                  type="primary"
                  size="large"
                  className="w-100"
                >
                  Close
                </Button>,
              ]}
            />
          </div>
        </Modal>
        <ConfirmInvoice data={data} />
      </>
    );
  }

  return (
    <>
      <section className="container reservation-form py-1">
        <h2 className="mt-3 top-header px-2">Reserve Your Table </h2>
        {step === 1 && (
          <div className="text-center px-2 py-3 rounded">
            <div className="table-wrap w-100">
              {seats > 0 &&
                (parseInt(seats) > 6
                  ? [...Array(6).keys()]
                  : [...Array(parseInt(seats)).keys()]
                )?.map((i: number) => (
                  <img
                    src={Plate}
                    alt=""
                    width="60"
                    key={i}
                    className={`plate${i} plate`}
                  />
                ))}

              <img src={Table} alt="" className="main-img" width="100%" />
            </div>
          </div>
        )}

        <Form
          ref={formRef}
          className="reservation-form px-1"
          layout="vertical"
          onFinish={onFinish}
          autoComplete="off"
        >
          <Form.Item hidden={step === 1 ? false : true}>
            <Form.Item
              name="no_of_occupancy"
              label="How Many People?"
              rules={[
                {
                  required: true,
                  message: "Please select no of seats!",
                },
              ]}
            >
              <Selector
                onChange={checkManual}
                columns={6}
                value={[seats?.toString() || "1"]}
                options={[
                  { label: "1", value: "1" },
                  { label: "2", value: "2" },
                  { label: "3", value: "3" },
                  { label: "4", value: "4" },
                  { label: "5", value: "5" },
                  { label: "6", value: "6" },
                  { label: seats > 6 ? seats?.toString() : "+", value: "plus" },
                ]}
              />
            </Form.Item>

            <div className="d-flex">
              <div className="col-4 pe-1">
                <Form.Item
                  label="Male"
                  name="male"
                  initialValue={0}
                  rules={[
                    {
                      required: true,
                      message: "Please input No of Male!",
                    },
                  ]}
                >
                  <Input
                    suffix={<UsergroupAddOutlined />}
                    placeholder="No"
                    size="large"
                    type="number"
                    min={"0"}
                  />
                </Form.Item>
              </div>
              <div className="col-4 pe-1">
                <Form.Item
                  initialValue={0}
                  label="Female"
                  name="female"
                  rules={[
                    {
                      required: true,
                      message: "Please Enter No of Female!",
                    },
                  ]}
                >
                  <Input
                    suffix={<UsergroupAddOutlined />}
                    placeholder="No"
                    size="large"
                    type="number"
                    min={"0"}
                  />
                </Form.Item>
              </div>
              <div className="col-4 pe-1">
                <Form.Item
                  label="Kids"
                  initialValue={0}
                  name="kids"
                  rules={[
                    {
                      required: true,
                      message: "Please input No of Kids!",
                    },
                  ]}
                >
                  <Input
                    suffix={<UsergroupAddOutlined />}
                    placeholder="No"
                    size="large"
                    type="number"
                    min={"0"}
                  />
                </Form.Item>
              </div>
            </div>

            <Form.Item
              label="Name"
              name="name"
              rules={[
                {
                  required: true,
                  message: "Please input your Name!",
                },
              ]}
            >
              <Input
                suffix={<UserOutlined />}
                placeholder="Enter Your Name"
                size="large"
              />
            </Form.Item>

            <Form.Item
              label="Contact No"
              name="phone"
              rules={[
                {
                  required: true,
                  message: "Please input your Contact No!",
                },
                { validator: checkPhone },
                {
                  min: 10,
                  max: 10,
                  message: "Contact Number must be of 10 digits.",
                },
              ]}
            >
              <Input
                suffix={<PhoneOutlined />}
                placeholder="Enter Your Contact No"
                size="large"
              />
            </Form.Item>

            <Form.Item
              label="Email"
              name="email"
              rules={[
                {
                  required: true,
                  message: "Please input your email!",
                },
                {
                  type: "email",
                  message: "Not a valid email",
                },
              ]}
            >
              <Input
                suffix={<MailOutlined />}
                placeholder="Enter Your Email Address"
                size="large"
              />
            </Form.Item>

            <div className="d-flex">
              <div className="col-6 pe-1">
                <Form.Item
                  label="Date"
                  name="date"
                  rules={[{ validator: checkDate }]}
                  onClick={() => setVisible(true)}
                >
                  <DatePickerComp setVisible={setVisible} visible={visible} />
                </Form.Item>
              </div>
              <div className="col-6 ps-1">
                <Form.Item
                  label="Time"
                  name="time"
                  onClick={() => setVisibleTime(true)}
                  rules={[{ validator: checkTime }]}
                >
                  <TimePicker
                    setVisible={setVisibleTime}
                    visible={visibleTime}
                  />
                </Form.Item>
              </div>
            </div>
          </Form.Item>

          <Form.Item hidden={step === 1 ? true : false}>
            <Form.Item
              name="table_type_id"
              label="Seat Option"
              onClick={() => setVisibleTable(true)}
              rules={
                step === 2
                  ? [
                      {
                        validator: checkType,
                      },
                    ]
                  : null
              }
            >
              <TableType setVisible={setVisibleTable} visible={visibleTable} />
            </Form.Item>

            <Form.Item name="comment" label="Comment">
              <TextArea
                rows={4}
                placeholder="Message Here"
                className="form-control"
              />
            </Form.Item>
          </Form.Item>
          <Form.Item>
            <div className="d-flex">
              <Button
                hidden={step === 1 ? true : false}
                onClick={() => setStep(1)}
                type="primary"
                size="large"
                className=" fw-bolder mb-4 mt-3 me-3"
              >
                <LeftOutlined className="f-13 v-m text-white" />
              </Button>
              <Button
                loading={loading}
                type="primary"
                size="large"
                htmlType="submit"
                className="fw-bolder mb-4 mt-3 w-100"
              >
                {step === 1 ? (
                  <>
                    {" "}
                    Next <RightOutlined className="f-14 v-m ms-1 text-white" />
                  </>
                ) : (
                  "Confirm"
                )}
              </Button>
            </div>
          </Form.Item>
        </Form>
      </section>
    </>
  );
};

export default App;
