import React, { useEffect, useState } from 'react';
import {
  Route,
  BrowserRouter as Router,
  Switch,
  useHistory,
  useLocation
} from "react-router-dom";
import { authUser } from "@/reducers/auth";

import Login from "@/pages/restaurant/auth/login";
import Dashboard from "@/pages/restaurant/Dashboard/index";
import DashboardMain from "@/pages/restaurant/Dashboard/Main";
import Waiting from "@/pages/restaurant/Dashboard/Waiting";
import { useDispatch, useSelector } from 'react-redux';
import { user } from "@/api/restaurant"
import { setAuthUser } from "@/reducers/auth";
import { Skeleton } from 'antd';
import Tables from '@/pages/restaurant/Dashboard/Tables';
import HistoryPage from '@/pages/restaurant/Dashboard/History';
import { PullToRefresh } from 'antd-mobile';
import { PullStatus } from 'antd-mobile/es/components/pull-to-refresh';

function App() {
  const history = useHistory();
  const location = useLocation();
  const AuthSelector = useSelector(authUser);
  let tab = new URLSearchParams(location.search).get('tab') || 1;
  let date = new URLSearchParams(location.search).get('date');

  const [loading, setLoading] = useState(true);
  const dispatch = useDispatch();
  
  const token = localStorage.getItem("user-token");

  useEffect(() => {


    if(token && !AuthSelector && location.pathname.match('/restaurant/')){
      user()
      .then( (res: any) => {

        let data = res;
        if(!data?.restaurant?.length)
          data.restaurant = [res?.restaurant];
    
        dispatch(setAuthUser(data));
        history.push({
           pathname: '/restaurant/dashboard',
           search: `tab=${tab.toString()}&date=${date || ''}`
        });
        setLoading(false);
      }).catch((err: any) => {
        localStorage.removeItem("user-token");
      })
    }else{
      if(!AuthSelector && location.pathname.match('/restaurant/')){
        history.push('/restaurant/login');
        setLoading(false);
      }
    }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [AuthSelector])
  
  const statusRecord: Record<PullStatus, string> = {
    pulling: 'Pulling',
    canRelease: 'Release...',
    refreshing: 'Loading...',
    complete: 'Completed...',
  }

  if(loading && location.pathname.match('/restaurant/')) return <div className="container-fluid p-5"><Skeleton active/> </div> 

  return (  
    <>
      <PullToRefresh onRefresh={(e) => {
        setLoading(true);
        user()
        .then( (res: any) => {
          dispatch(setAuthUser(res));
          setLoading(false);
        }).catch((err: any) => {
        })
      }}
      renderText={status => {
        return <div>{statusRecord[status]}</div>
      }}
      >
        <Router basename="/restaurant">
          <Switch>
            <Route path="/login" exact component={Login}></Route>  
            <Route path="/analytic" exact component={DashboardMain}></Route>  
            <Route path="/dashboard" exact component={Dashboard}></Route>  
            <Route path="/table" exact component={Tables}></Route>  
            <Route path="/waiting" exact component={Waiting}></Route>  
            <Route path="/history" exact component={HistoryPage}></Route>  
          </Switch>
        </Router> 
      </PullToRefresh>
  </>);
}

export default App;
