
import { Input } from "antd";
import { DatePicker } from "antd-mobile";
import { CalendarOutlined } from '@ant-design/icons'
import moment from 'moment'
import { useState, useCallback, useEffect } from "react";


export default function DatePickerComp ({initialValue, value= {}, onChange, setVisible, visible, suffix=true}) {
    const [number, setNumber] = useState(initialValue ? moment(initialValue).format('DD-MM-YYYY') : '');

    useEffect(() => {
      if(initialValue) {
        let values = moment(initialValue).format('DD-MM-YYYY');
        triggerChange(values);
      }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [initialValue]);

    const triggerChange = (changedValue) => {
      onChange?.({
        number,
        ...value,
        ...changedValue,
      });
    };
  
    const onNumberChange = (e) => {
      let newNumber = moment(e).format('DD-MM-YYYY');
      if (!('number' in value)) {
        setNumber(newNumber);
      }
  
      triggerChange({
        number: newNumber,
      });
    };
  
    const labelRenderer = useCallback((type: string, data: number) => {
      var monthNames = [ "January", "February", "March", "April", "May", "June",
      "July", "August", "September", "October", "November", "December" ];
      switch (type) {
        case 'year':
          return data
        case 'day':
          return data
        case 'month':
          return monthNames[--data]
        default:
          return data
      }
    }, [])
  
    return ( 
      <span>
        <DatePicker
          title="Reservation Date"
          confirmText="Confirm"
          cancelText="Cancel"
          min={new Date()}
          renderLabel={labelRenderer}
          visible={visible}
          onClose={() => setVisible(false)}
          onSelect={onNumberChange}
        />  
        <Input value={value?.number || number} disabled placeholder="Select Date" suffix={suffix && <CalendarOutlined/>}/> 
      </span>
      )
  }
  